import { newArticle, editArticle } from '@/api/edit'
import { getArticle } from '@/api/article'
export default {
  // 在路由匹配组件渲染之前先执行中间件处理
  middleware: ['authenticated'],
  name: 'EditorIndex',
  data () {
    return {
      article: {
        title: '',
        body: '',
        description: '',
        tagList: ''
      },
      tagList: []
    }
  },
  async asyncData (context) {
    console.log(context)
    if (!context.params.slug) {
      return {
        article: {}
      }
    }
    const { data } = await getArticle(context.params.slug)
    const { article } = data
    const tagList = article.tagList
    article.tagList = ''
    return {
      tagList,
      article
    }
  },
  methods: {
    async uploadArticle () {
      this.article.tagList = this.article.tagList === '' ? this.tagList : this.article.tagList
      if (this.article.slug) {
        const { data } = await editArticle({article: this.article})
        this.$router.push('/article/'+ data.article.slug)
      } else {
        const { data } = await newArticle({article: this.article})
        this.$router.push('/article/'+ data.article.slug)
      }
    },
    onSubmit () {
      this.tagList.push(this.article.tagList)
      this.article.tagList =  ''
    }
  },
  watchQuery: ['slug'],
}
